import { InfoOutlined, PlayArrow } from '@material-ui/icons';
import React from 'react';
import "./featured.scss";

const Featured = ({type}) => {
    return (
        <div className="featured">
            {type && (
                <div className="category">
                    <span>{type === "movie" ? "Movies" : "Series"}</span>
                    <select name="genre" id="genre">
                        <option> Genre</option>
                        <option value="adventure">Adventure</option>
                        <option value="comedy">Comedy</option>
                        <option value="crime">Crime</option>
                        <option value="fantasy">Fantasy</option>
                        <option value="historical">Historical</option>
                        <option value="horror">Horror</option>
                        <option value="romance">Romance</option>
                        <option value="sci-fi">Sci-fi</option>
                        <option value="thriller">Thriller</option>
                        <option value="western">Western</option>
                        <option value="animation">Animation</option>
                        <option value="drama">Drama</option>
                        <option value="documentary">Documentary</option>
                    </select>
                </div>
            )}
            <img src="https://cdn.thetealmango.com/wp-content/uploads/2021/07/lucifer-1.jpg" alt="" />
            <div className="info">
                {/* <img src="https://www.pngfind.com/pngs/m/616-6166782_lucifer-netflix-logo-png-transparent-png.png" alt="" /> */}
                <span className="desc">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                Maiores sapiente neque facilis reprehenderit tempore, deleniti aperiam in suscipit nemo blanditiis,
                est nam consectetur saepe voluptatem assumenda, voluptas animi cumque voluptatibus.</span>
                <div className="buttons">
                    <button className="play">
                        <PlayArrow />
                        <span>Play</span>
                    </button>
                    <button className="more">
                        <InfoOutlined />
                        <span>Info</span>
                    </button>
                </div>

            </div>
        </div>
    )
}

export default Featured;
